* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background-color: plum;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.birthday-heading {
  margin-left: 2rem;
  padding-top: 1rem;
  font-size: 1.5rem;
}

.birthday-container {
  height: auto;
  width: 400px;
  background-color: white;
  border-radius: 0.8rem;
  padding-bottom: 1rem;
}

img {
  height: 50px;
  width: 50px;
  border-radius: 70%;
  object-fit: cover;
}
.list-items {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  margin-left: 2rem;
}

.content-box {
  display: flex;
  margin-top: 1rem;
}

.person-details {
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: flex-start;
  row-gap: 0.3rem;
  margin-left: 0.4rem;
}
.person-details p {
  margin: 0;
  padding: 0;
}

.person-details span {
  margin: 0;
  padding: 0;
}
.btn-box {
  margin-top: 1rem;
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
div button {
  border: none;
  width: 80%;
  height: 30px;
  background-color: blueviolet;
  color: white;
}
